import {
  GET_ALBUMS,
  GET_ALBUM,
  ADD_IMAGE,
  CREATE_ALBUM,
  SHOW_DELETE_POPUP,
  SHOW_IMAGE_POPUP,
  DELETE_ALBUM,
  DELETE_IMAGE
} from "../actions/albums";

export default (state, action) => {
  console.log("action.payload", action.payload);
  switch (action.type) {
    case GET_ALBUMS:
      return {
        ...state,
        albums: action.payload.albums
      };
    case GET_ALBUM:
      return {
        ...action.payload
      };
    case CREATE_ALBUM:
      return {
        ...action.payload
      };
    case ADD_IMAGE:
      return {
        ...state,
        ...action.payload
      };
    case SHOW_DELETE_POPUP:
      if (state.showDeletePopup == true) {
        return {
          ...state,
          showDeletePopup: false
        };
      } else {
        return {
          ...state,
          togglePopup: true,
          imageToDelete: action.payload
        };
      }
    case SHOW_IMAGE_POPUP:
      return {
        ...action.payload
      };
    case DELETE_ALBUM:
      return {
        ...action.payload
      };
    case DELETE_IMAGE:
      return {
        ...action.payload
      };
    default:
      return state;
  }
};
